import { mapGetters } from "vuex";
import { SET_LRYL_BTN } from "../../store/nameSpace";
export default {
  data() {
    return {
      name: "lrylBtnControltrue"
    };
  },
  computed: {
    ...mapGetters(["lrylBtnControl"])
  },
  created() {
    this.$store.commit(SET_LRYL_BTN, true);
  }
};
