import { mapGetters } from "vuex";

export default {
  data() {
    return {
      name: "lryl",
      lrylModelData: []
    };
  },
  computed: {
    ...mapGetters(["ylData", "lrData"])
  },
  methods: {
    setlrylNumberList() {
      this.numberList.map(item => {
        this.ylData.find(itemInner => {
          if (itemInner.type === item.type) {
            item.ylList = itemInner.data;
            return true;
          }
        });
      });
      this.numberList.map(item => {
        this.lrData.find(itemInner => {
          if (itemInner.type === item.type) {
            item.lrList = itemInner.data;
            return true;
          }
        });
      });
    }
  },
  watch: {
    ylData() {
      this.setlrylNumberList(); // 刷新的时候会执行
    },
    lrData() {
      this.setlrylNumberList(); // 刷新的时候会执行
    },
    numberList(val) {
      this.setlrylNumberList(); // 切换玩法的时候会执行
    }
  },
  created() {
    // this.setlrylNumberList()
  }
};
