<template>
  <ul class="choice-number-list">
    <li v-for="(item, index) in numberList" :key="index">
      <div class="top top_1">
        <div class="top">
          <h4>{{ item.type }}</h4>
          <div
            class="balls clear-fix"
            :style="{
              'max-width': item.numberList.length > 12 ? '700px' : 'auto'
            }"
          >
            <span
              class="ball"
              v-for="(ball, innerIndex) in item.numberList"
              :class="{
                selected: ball.isSelected,
                [ball.color]: true
              }"
              @click="clickBall(ball, index, innerIndex)"
              :key="innerIndex"
            >
              {{ ball.label }}</span
            >
          </div>
        </div>
        <div class="types clear-fix" v-if="item.showBtn">
          <span
            class="select_balls_change"
            :class="{ 'now-hov': item.isSelectAll }"
            @click="
              selectAllBalls(
                item.numberList,
                'isSelected',
                item,
                'isSelectAll',
                numberAttrNameList,
                calculateBetCounts,
                undefined,
                index
              )
            "
            >{{ $t("gameTemplate.whole") }}</span
          >
          <span
            class="select_balls_change"
            :class="{ 'now-hov': item.isSelectBig }"
            @click="
              selectBigBalls(
                item.numberList,
                'isSelected',
                item,
                'isSelectBig',
                numberAttrNameList,
                calculateBetCounts,
                undefined,
                index
              )
            "
            >{{ $t("gameTemplate.big") }}</span
          >
          <span
            class="select_balls_change"
            :class="{ 'now-hov': item.isSelectSmall }"
            @click="
              selectSmallBalls(
                item.numberList,
                'isSelected',
                item,
                'isSelectSmall',
                numberAttrNameList,
                calculateBetCounts,
                undefined,
                index
              )
            "
            >{{ $t("gameTemplate.small") }}</span
          >
          <span
            class="select_balls_change"
            :class="{ 'now-hov': item.isSelectOdd }"
            @click="
              selectOddBalls(
                item.numberList,
                'isSelected',
                item,
                'isSelectOdd',
                numberAttrNameList,
                calculateBetCounts,
                undefined,
                index
              )
            "
            >{{ $t("gameTemplate.odd") }}</span
          >
          <span
            class="select_balls_change"
            :class="{ 'now-hov': item.isSelectEven }"
            @click="
              selectEvenBalls(
                item.numberList,
                'isSelected',
                item,
                'isSelectEven',
                numberAttrNameList,
                calculateBetCounts,
                undefined,
                index
              )
            "
            >{{ $t("gameTemplate.even") }}</span
          >
          <span
            class="select_balls_change"
            @click="
              clearAllBallsSelect(
                item.numberList,
                'isSelected',
                item,
                numberAttrNameList,
                calculateBetCounts
              )
            "
            >{{ $t("gameTemplate.clear") }}</span
          >
        </div>
      </div>
      <div
        v-show="nowActiveWayInfoDetail['missing_data'] && showYlPlay"
        class="center clear-fix"
      >
        <h4 :style="{ marginRight: lang === 'en' ? '44px' : '28px' }">
          {{ $t("gameTemplate.currentOmission") }}
        </h4>
        <div class="numbers clear-fix">
          <span v-for="(ball, innerIndex) in item.ylList" :key="innerIndex">{{
            ball.count
          }}</span>
        </div>
      </div>
      <div
        v-show="nowActiveWayInfoDetail['hot_data'] && showLrPlay"
        class="bottom clear-fix"
      >
        <h4 :style="{ marginRight: lang === 'es' ? '7px' : '19px' }">
          {{ $t("gameTemplate.coldAndHot120") }}
        </h4>
        <div class="numbers clear-fix">
          <span v-for="(ball, innerIndex) in item.lrList" :key="innerIndex">{{
            ball.count
          }}</span>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
import sscMixins from "../../credit/mixins/ssc_mixin";
import resetBalls from "../../credit/mixins/balls-reset";
import {
  SET_SSC_SELECTED_COUNTS,
  SET_HIGHEST_PRIZE_LEVEL,
  SET_SELECTED_LINE_COUNTS
} from "../../../store/nameSpace";
import { mapGetters } from "vuex";
import lrylMixins from "../../credit/minxins-lryl";
import minxinsLrylBtnControl from "../../credit/minxins-lrylbtncontroltrue";

// 引入核心的算法文件
import { checkLotteryCounts, sortNumber } from "../../../common/js/LotteryBet";

let nowCacheData = [];
export default {
  mixins: [sscMixins, resetBalls, lrylMixins, minxinsLrylBtnControl],
  // props: [ 'flagCount', 'getShoppingCarList' ],
  props: ["flagCount", "getShoppingCarList", "nowActiveWayInfoDetail"],
  data() {
    return {
      numberList: [],
      numberAttrNameList: [
        "isSelectAll",
        "isSelectBig",
        "isSelectSmall",
        "isSelectOdd",
        "isSelectEven"
      ],
      selectedCounts: 0
    };
  },
  computed: {
    ...mapGetters([
      "showYlPlay",
      "lrData",
      "ylData",
      "showLrPlay",
      "showDataFlag",
      "lang"
    ])
  },
  methods: {
    clickBall(ball, index, innerIndex) {
      ball.isSelected = !ball.isSelected;
      this.calculateBetCounts(index, innerIndex);
    },
    calculateBetCounts(index, innerIndex) {
      const rowLimit = this.nowActiveWayInfoDetail[`row_limit`];
      const ballLimits = (
        this.nowActiveWayInfoDetail[`ball_limits`] || ""
      ).split(",");
      if (rowLimit && index !== undefined) {
        let delListIndex = false;
        if (
          this.numberList[index].numberList.find(item => {
            return item.isSelected;
          })
        ) {
          const addIndex = nowCacheData.indexOf(index);
          if (addIndex === -1) {
            nowCacheData.push(index);
          }
        } else {
          const delIndex = nowCacheData.indexOf(index);
          if (delIndex !== -1) {
            nowCacheData.splice(delIndex, 1);
          }
        }
        if (nowCacheData.length > rowLimit) {
          delListIndex = nowCacheData.shift();
        }
        if (delListIndex !== false) {
          this.numberList.find((item, index) => {
            if (index === delListIndex) {
              item.numberList.map(itemInner => {
                itemInner.isSelected = false;
              });
              this.numberAttrNameList.map(nameAttr => {
                item[nameAttr] = false;
              });
              return true;
            }
          });
        }
      }

      const nowSelectNumber = [];
      const nowPrizesInfo = [];
      this.numberList.map((item, index) => {
        const maxBallCounts = ballLimits[index] || ballLimits[0] || undefined;
        const innerArr = [];
        let count = 0;
        const nowNumbers = item.numberList.length - 1;
        item.numberList.map(itemInner => {
          if (itemInner.isSelected) {
            count++;
          }
        });
        // 每行最大注数的选择拦截
        item.numberList.map(() => {
          if (count > maxBallCounts) {
            count--;
            let randIndex = Math.round(Math.random() * nowNumbers);
            while (
              innerIndex === randIndex ||
              !item.numberList[randIndex].isSelected
            ) {
              randIndex = Math.round(Math.random() * nowNumbers);
            }
            item.numberList[randIndex].isSelected = false;
          }
        });
        item.numberList.map(itemInner => {
          if (itemInner.isSelected) {
            nowPrizesInfo.push(+itemInner.prizeLevel);
            innerArr.push(itemInner);
          }
        });
        nowSelectNumber.push(innerArr);
      });
      // 重置状态管理中的最高奖级信息
      if (nowPrizesInfo.length) {
        this.$store.commit(
          SET_HIGHEST_PRIZE_LEVEL,
          `${Math.min.apply(this, nowPrizesInfo)}`
        );
      } else {
        this.$store.commit(SET_HIGHEST_PRIZE_LEVEL, ``);
      }

      const result = checkLotteryCounts({
        // 用户选择的数字
        betData: nowSelectNumber.map(item =>
          item.map(itemInner => itemInner.value)
        ),
        areaSplitChar: this.nowActiveWayInfoDetail["area_split_char"],
        ballSplitChar: this.nowActiveWayInfoDetail["ball_split_char"],
        methodDetail: this.nowActiveWayInfoDetail["count_function"]
      });
      result.labelInfo = sortNumber(
        this.nowActiveWayInfoDetail["area_split_char"],
        this.nowActiveWayInfoDetail["ball_split_char"],
        nowSelectNumber.map(item => item.map(itemInner => itemInner.label))
      );
      this.$store.commit(SET_SELECTED_LINE_COUNTS, result.selectLineCounts);
      this.selectedCounts = result.betCount;
      this.$store.commit(SET_SSC_SELECTED_COUNTS, this.selectedCounts);
      return result;
    },
    resetNumberList() {
      nowCacheData = [];
      this.numberList.map(item => {
        item.isSelectAll = false;
        item.isSelectBig = false;
        item.isSelectSmall = false;
        item.isSelectOdd = false;
        item.isSelectEven = false;
        item.numberList.map(itemInner => {
          itemInner.isSelected = false;
        });
      });
    },
    // 将数据更新为配置文件中的相关数据
    getNumberListInfo() {
      this.numberList = [];
      const nowPlayWayInfo = this.nowActiveWayInfoDetail;
      if (
        !nowPlayWayInfo ||
        !nowPlayWayInfo["ball_strings"] ||
        !nowPlayWayInfo["ball_values"]
      )
        return false;
      const plateRows = nowPlayWayInfo["plat_rows"];

      // 获取到配置文件中需要显示label值
      let ballStrings = nowPlayWayInfo["ball_strings"].split("|");

      // 获取到配置文件中显示值对应的value
      let ballValues = nowPlayWayInfo["ball_values"].split("|");

      // 获取到配置文件中对应显示的title
      let rowTitles = nowPlayWayInfo["row_titles"].split(",");

      // 获取当前玩法的奖级数据
      let ballLevels = nowPlayWayInfo["ball_prize_levels"].split(",");

      // 获取是否显示菜单按钮的相关字段
      let shortcut = nowPlayWayInfo["shortcut"].split(",");

      // 获取到颜色
      let ballColors = nowPlayWayInfo["ball_color"];

      // 获取冷热遗漏的显示位置
      let position = nowPlayWayInfo["position"];

      if (ballLevels.length === 1 && !ballLevels[0]) {
        ballLevels = "1";
      }
      for (let i = 0; i < plateRows; i++) {
        const isShowBtn =
          shortcut[i] === undefined ? +shortcut[0] : +shortcut[i];
        let lrList = [];
        let ylList = [];
        if (!Number.isNaN(+position[i])) {
          lrList =
            (
              this.lrData.find(lrItem => +position[i] === +lrItem.position) ||
              {}
            ).data || [];
          ylList =
            (
              this.ylData.find(lrItem => +position[i] === +lrItem.position) ||
              {}
            ).data || [];
        }
        this.numberList.push({
          type: rowTitles[i],
          showBtn: !!isShowBtn,
          isSelectAll: false,
          isSelectBig: false,
          isSelectSmall: false,
          isSelectOdd: false,
          isSelectEven: false,
          numberList: [],
          ylList,
          lrList
        });
      }
      if (ballStrings.length === ballValues.length && ballValues.length === 1) {
        ballStrings = ballStrings[0].split(",");
        ballValues = ballValues[0].split(",");
        this.numberList.map((item, index) => {
          item.type = rowTitles[index];

          for (
            let i = 0;
            i < Math.min(ballStrings.length, ballValues.length);
            i++
          ) {
            const prizeLevel =
              typeof ballLevels === "string" ? "1" : ballLevels[i];
            let color =
              (ballColors &&
                ballColors[index] &&
                ballColors[index][ballStrings[i]]) ||
              "";
            item.numberList.push({
              isSelected: false,
              label: ballStrings[i],
              color,
              value: ballValues[i],
              prizeLevel
            });
          }
        });
      } else {
        // 如果拆分为多个格式情况
        ballStrings = ballStrings.map(numberItem => {
          return numberItem.split(",");
        });
        ballValues = ballValues.map(numberItem => {
          return numberItem.split(",");
        });
        this.numberList.map((item, index) => {
          item.type = rowTitles[index];
          for (
            let i = 0;
            i < Math.min(ballStrings[index].length, ballValues[index].length);
            i++
          ) {
            const prizeLevel =
              typeof ballLevels === "string" ? "1" : ballLevels[i];
            let color =
              (ballColors &&
                ballColors[index] &&
                ballColors[index][ballStrings[index][i]]) ||
              "";
            item.numberList.push({
              isSelected: false,
              label: ballStrings[index][i],
              color,
              value: ballValues[index][i],
              prizeLevel
            });
          }
        });
      }
    }
  },
  watch: {
    flagCount() {
      const result = this.calculateBetCounts();
      if (result.result !== 0) {
        return this.getShoppingCarList(false, false, 0);
      }
      this.getShoppingCarList(
        result.numberStr,
        this.selectedCounts,
        undefined,
        result.labelInfo
      );
    },
    nowActiveWayInfoDetail(val) {
      this.getNumberListInfo();
      this.setlrylNumberList();
    }
  },
  created() {
    this.getNumberListInfo();
    setTimeout(() => {}, 3000);
  }
};
</script>

<style scoped>
/* @import "../../../statics/ssc-plate.css"; */
@import "../statics/css/plate.css";
</style>
